.nav {
    position: absolute;
    top: -10%;
    left: 0;
    width: fit-content;
    height: 100px;
    z-index: 1;
}

.container-nav {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    height: fit-content;
    /* background-color: blue; */
}

.containner-list-icon {
    width: fit-content;
    padding: 10px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: #fff;
    border-radius: 10px;
    margin-top: 10px;
    box-shadow: 0px 5px 20px #e0e0e0;
}

a {
    color: #959595;
    transition: .5s ease-in-out;
}

a:hover {
    color: #2d2d2d;
}
